.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
}

input, select {
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  padding: 20px;
}

.form-card {
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 40px;
  max-width: 450px;
  width: 100%;
}

.form-card h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #333;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 16px;
}

.form-group label {
  font-weight: 600;
  color: #333;
}

.form-group input {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s;
}

.form-group input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 5px rgba(52, 152, 219, 0.2);
}

.form-group input:disabled {
  background: #f5f5f5;
  cursor: not-allowed;
}

.form-card form {
  display: flex;
  flex-direction: column;
}

.form-card button {
  padding: 12px;
  background: #3498db;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s;
  margin-top: 10px;
}

.form-card button:hover:not(:disabled) {
  background: #2980b9;
}

.form-card button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.error-message {
  background: #fee;
  color: #c33;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 20px;
  border-left: 4px solid #c33;
}

.loading-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 40px 20px;
  text-align: center;
}

.loading-message p {
  font-size: 16px;
  color: #333;
}

.spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.form-footer {
  text-align: center;
  margin-top: 20px;
  color: #666;
}

.form-footer a {
  color: #3498db;
  text-decoration: none;
  font-weight: 600;
}

.form-footer a:hover {
  text-decoration: underline;
}

/* ===== Progress Steps ===== */
.progress-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}

.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.step-number {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #e0e0e0;
  color: #666;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s;
}

.step.active .step-number {
  background: #3498db;
  color: white;
}

.step-label {
  font-size: 12px;
  color: #999;
}

.step.active .step-label {
  color: #3498db;
  font-weight: 600;
}

.step-line {
  width: 50px;
  height: 3px;
  background: #e0e0e0;
  margin: 0 10px;
  margin-bottom: 20px;
  transition: all 0.3s;
}

.step-line.active {
  background: #3498db;
}

/* ===== OTP Input ===== */
.otp-input {
  font-size: 24px !important;
  letter-spacing: 8px;
  text-align: center;
}

.otp-info {
  text-align: center;
  color: #666;
  margin-bottom: 20px;
  line-height: 1.5;
}

/* ===== Success Message ===== */
.success-message {
  background: #e8f5e9;
  color: #2e7d32;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 20px;
  border-left: 4px solid #2e7d32;
}

/* ===== Secondary Button ===== */
.btn-secondary {
  background: #f5f5f5 !important;
  color: #333 !important;
  border: 1px solid #ddd !important;
}

.btn-secondary:hover:not(:disabled) {
  background: #e0e0e0 !important;
}

/* ===== Link Button ===== */
.btn-link {
  background: transparent !important;
  color: #3498db !important;
  font-weight: normal !important;
}

.btn-link:hover:not(:disabled) {
  background: transparent !important;
  text-decoration: underline;
}

